.meet-our-team {
    max-width: 800px;
    margin: 0 auto;
    padding: 50px 20px;
    text-align: center;
    h1{
        color:brown;
        font-family: Arial, Helvetica, sans-serif;
        @media(max-width:786px){
          font-size: 2rem;
          font-weight: 800;
          span{
            display: none !important;
          }
        }
    }
    .paragraph{
        font-family: Arial, Helvetica, sans-serif;
        color:rgba(1, 0, 0, 0.5);
        font-weight: 600;
        @media(max-width:786px){
          display: none !important;
        }

    }
  }
  
  .grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 20px;
    margin-top: 50px;
  }
  
  .grid-item {
    position: relative;
    overflow: hidden;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    transition: transform 0.3s ease;
  }
  
  .grid-item:hover {
    transform: translateY(-10px);
  }
  
  .image-container {
    position: relative;
    overflow: hidden;
    height: 350px;
    @media(max-width:786px){
        height: 400px !important;
    }
  }
  
  .image-container img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 0 8%;
    transition: transform 0.3s ease;
  }
  
  .image-container:hover img {
    transform: scale(1.1);
  }
  
  .team-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    transition: opacity 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .grid-item:hover .overlay {
    opacity: 1;
  }
  
  .overlay a {
    color: #fff;
    font-size: 24px;
    transition: transform 0.3s ease;
  }
  
  .overlay a:hover {
    transform: scale(1.2);
  }
  
  .text-container {
    padding: 20px;
    text-align: center;
    h2{
        color:#003049 !important;
        font-family: Arial, Helvetica, sans-serif;
        font-weight: 800;
        @media(max-width:786px){
          font-size:1.8rem !important;

         }
        }
        p{
            color:#ff0000 !important;
            font-family: Arial, Helvetica, sans-serif;
           @media(max-width:786px){
            font-size:1.3rem !important;
            font-weight:600
           
           }
        }
    }
  
  /*TEAM DETAILS HERE*/
  .team-members-detail-container {
    max-width: 800px;
    margin: 0 4rem;
    padding: 50px 0;
    background:#008800;
    
  }
  
  .team-members-details{
    display: flex;
   flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 700px;
    min-height:400px;
    height:auto;
  }
  
  .member {
    width: 100%;
    margin: 20px;
    padding: 20px;
    border: 1px solid #ddd;
    text-align: center;
  }
  
  .member img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin-bottom: 20px;
  }
  
  .member h3 {
    margin-top: 0;
  }
  
  .member p {
    margin-bottom: 10px;
  }
